.Allsectionconnexion {
    background: linear-gradient(180deg, #D2E7F7 0%, #FFFFFF 51.04%);
}

h1 {
    margin    : 30px 0 50px 0;
    text-align: center;
}

input.input-field {
    width: 120px;
}

input.input-picture{
    margin-top: 5px;
}

.form-style-2 {
    max-width: 500px;
    padding  : 20px 12px 10px 20px;
    font     : 13px Arial, Helvetica, sans-serif;
}

.form-style-2 label {
    display: block;
    margin : 0px 0px 15px 0px;
}

.form-style-2 label>span {
    width        : 100px;
    font-weight  : bold;
    float        : left;
    padding-top  : 8px;
    padding-right: 5px;
}


.form-style-2 input.input-field,
.form-style-2 .select-field {
    width: 68%;
}

.form-style-2 input.input-field,
.input {
    box-sizing           : border-box;
    -webkit-box-sizing   : border-box;
    -moz-box-sizing      : border-box;
    border               : 1px solid #C2C2C2;
    box-shadow           : 1px 1px 4px #EBEBEB;
    -moz-box-shadow      : 1px 1px 4px #EBEBEB;
    -webkit-box-shadow   : 1px 1px 4px #EBEBEB;
    border-radius        : 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius   : 5px;
    padding              : 7px;
    outline              : none;
    width                : auto;
    background           : #FFFFFF;
    box-shadow           : inset 0px 5px 8px rgba(0, 0, 0, 0.61);
    border-radius        : 5px;
}

.form-style-2 .input-field:focus,
.filed,
.form-style-2 .select-field:focus {
    border: none;
}

.form-style-2 {
    margin-top: 5%;
}

.Allsectionconnexion {
    display       : flex;
    flex-direction: column;
}

.btn1inscrip {
    display        : flex;
    flex-direction : row;
    justify-content: center;
    margin-top     : 30px;
}

#button1inscrip {
    background   : #DEA9FF;
    box-shadow   : inset 0px 5px 8px rgba(0, 0, 0, 0.61);
    border-radius: 30px;
    border       : none;
    font-family  : 'Saira', sans-serif;
    font-weight  : 600px;
    width        : 91px;
    height       : 21px;
    margin-right : 20px;

}

.Allsectionconnexion1 {
    display        : flex;
    flex-direction : row;
    justify-content: center;
}

.titreH1connexion {
    margin     : 30px 0 50px 0;
    text-align : center;
    font-family: 'Saira', sans-serif;
    font-style : normal;
    font-weight: 600;
}

.filed {
    position: relative;
    top     : 5px;
    right   : 90px;
}

.imagecontconnexion {
    display        : flex;
    flex-direction : row;
    justify-content: center;
    margin-top     : 30px;
}


.parent-div {
    display : inline-block;
    position: relative;
    overflow: hidden;
}

.parent-div input[type=file] {
    left     : 0;
    top      : 0;
    opacity  : 0;
    position : absolute;
    font-size: 90px;
}

.btn-uploadconnexion {
    background-color: #fff;
    border          : 1px solid #000;
    color           : #000;
    font-size       : 15px;
    height          : 30px;
    width           : 75px;
    background      : #C4C4C4;
    border-radius   : 7px;

}

#parent-div {
    position     : relative;
    right        : 76px;
    top          : 10px;
    border-radius: 7px;

}

/**/
.roundimageconnexion {
    height       : 200px;
    width        : 200px;
    border-radius: 50%;
    overflow     : hidden;
    border       : 1px solid grey;
    background   : #C4C4C4;

}

#photo {
    height: 100%;
    width : 100%;
}